import React, { Component } from 'react';
import {MapTo} from '@adobe/aem-react-editable-components';


const FormButtonEditConfig = {
    emptyLabel: 'Form Button',

    isEmpty: function (props) {
        return !props || !props.text || props.text.trim().length < 1;
    }
};

const FormButton = (props) => {	

     const {
        type, 
        cusClass, 
        disabled, 
        name, 
        title,
        isInEditor,
        callback
    } = props

    const handleCallback = () => {
        if(callback && !isInEditor ){
            props.callback(props);
        }
    };

	
	return <button  
                type={type}
                className={`bsc-button ${cusClass}`}
                disabled={disabled}
                name={name}
                title={title} 
                onClick={() => handleCallback()}
            >
                {title}
            </button>;
	
}

export default MapTo('wknd-spa-react/components/form/button')(FormButton, FormButtonEditConfig);
