import React, { Component } from 'react';
import {
    MapTo, AuthoringUtils, Utils
} from '@adobe/aem-react-editable-components';
import { ComponentMapping } from '@adobe/aem-spa-component-mapping';
import {Link, useHistory } from 'react-router-dom';
import { createBrowserHistory } from 'history';
const ContainerConfig = {
    emptyLabel: 'Custom Form Container',

    isEmpty: function(props) {
        return !props || !props.cqItemsOrder || props.cqItemsOrder.length === 0;
    }


};


export const RenderComponentsList = ({
  cqItemsOrder = [],
  cqItems = {},
  isInEditor = AuthoringUtils.isInEditor(),
  ...props
}: ContainerProps): ReactElement[] => {
  const componentMapping = props.componentMapping || ComponentMapping;
  const components: Array<ReactElement> = [];
  cqItemsOrder.forEach((itemKey: string) => {
    const itemProps = Utils.modelToProps(cqItems[itemKey]);
    if (itemProps && itemProps.cqType) {
      const ItemComponent: React.ElementType = componentMapping.get(itemProps.cqType);
      if (ItemComponent) {
        components.push(
          <ItemComponent 
            key={itemProps.id}
            isInEditor={isInEditor}
            {...itemProps}
            callback={props.callback}
          />,
        );
      } else {
        console.error('Component not mapped for resourcetype:', itemProps.cqType);
      }
    }
  });
  return components;
};



const ActivityCalculator = (props) => {    
    const history = useHistory();
    const callback = (callBackObj) => {
       console.log("callBackObj ", callBackObj);
       const AcContext = React.createContext({successMsg: "Form has been submitted successfully"});


       if(callBackObj.name === "next"){
            history.push("faqs.html");
       }
    }

    return (<div>
                <Link to="/content/wknd-spa-react/us/en/faqs.html">
                    <div>WKND</div>
                </Link>
                <RenderComponentsList {...props} callback={callback} />
            </div>)
    
}

export default MapTo('wknd-spa-react/components/form/formcontainer')(ActivityCalculator, ContainerConfig);
